body{
  text-align: center;
  background-color: #F8F8F8;
  margin: auto;
  width: 100%;
  font-family: 'Roboto', sans-serif;
}

h3{
  color: black;
}

h2{
  padding: 0;
  margin: 0;
}

.header-outer{
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 100;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 0px;
}

.header{
    display: -ms-flexbox;
    display: flex;
    width: 90%;
    margin: 0 auto;
    -ms-flex-direction: column;
        flex-direction: column;
}


.header h1{
  color: #840017;;
}

.header h1:hover{
  color: white;
}

h3{
  color: #840017;
}

h3:hover{
  color: #3f3f3f;
}

.nav{
  width: 95%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
}


.nav li a{
  display: block;
  text-decoration: none;
  list-style-type: none;
  color: white;   
  margin: 5px auto;
  color: #840017;
}

.nav li a:hover{
  color: white;
}

.is-current{  
  border-bottom: 2px solid #840017;
}

.is-current:hover{  
  border-bottom: 2px solid white;
}

.intro{  
  width: 90%;
  margin: 140px auto 0;
}

.portfolio-image{
  padding: 10px 0 10px 0;
}

#main-avatar{
  border-radius: 50%;
}

.homeslide-nav{
  display: none;
}

.slide-nav{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.slide-nav p{
  cursor: pointer;
}

#portfolio{
  background-color: white;
  padding: 40px 0 20px 0;
  width: 100%;
  color: black;  
  z-index: 90;
}

#portfolio-section{
  z-index: 90;
  padding-top: 100px;
}

#portfolio-768{
  display: none;
}

#portfolio h3{
  padding-bottom:10px;

}

#portfolio h2{
  margin-top: 16px;
}

.portfolio-item-links-big{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.card{
  width: 90%;
  margin: 45px auto;
}

#portfolio-min img{
  height: 166px;
  width: 255px;
}

#portfolio-skills{
  background-color: black;
  color: black;
  height: 500px;
  width: 100%;
  z-index: 100;
}

#skills-section{
  margin-bottom: 150px;
}

#skills{
  background-color: white;
  color: black;
  width: 100%;
  margin: 10px auto;
  padding: 60px 0 70px 0;
  line-height: 200%;
}

#skills h3{
  padding-bottom:10px;
}

hr{
  color: grey;
  background-color: grey;
  border-color: grey;

}

#contact{
  width: 90%;
  margin: auto;
  border-bottom: 1px solid grey;
  padding-bottom: 100px;
  padding-top: 50px;
}

footer{
  width: 90%;
  margin: auto;
}

.footername{
  float:left;
}
.footerlink{
  float:right;
}

a{
  text-decoration: none;
  color: white;
}

#contact--icons{
  padding-bottom: 30px;
}

.twitter{
  position: relative;
  top: 15px;
  cursor: pointer;
}

.linkedin{
  margin-right: 15px;
  cursor: pointer;
}

.email{
  cursor: pointer;
}

footer{
  color: #840017;
}

footer nav{
  display: block;
  float: right;
}
.bottom_nav{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.bottom_nav ul li{
  margin: 0 0 0 20px;
}

.bottom_nav ul{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;

}

.bottom_nav ul{
  list-style-type: none;
}

.card a{
  color: black;
  cursor: pointer;
}



@media (min-width: 768px){

  .header{
    -ms-flex-direction: row;
        flex-direction: row
  }

  .nav{
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .main-nav{
    margin-left: auto;
    margin-top: 24px;
    display:-ms-flexbox;
    display:flex;
  }

  .nav li{
    text-align: center;
    color: white;
    list-style-type: none;      
    margin: 0 10px;
  }

  .nav li a{   
    padding: 0;
  }
  
  .homeslide-nav{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;

  }

  .homeslide-nav p{
    cursor: pointer;
  }

  .intro{
      margin: 82px auto 0px;
      padding-bottom: 10px;
      z-index: 1;
  }

  .intro a{
    color: black;
  }
  
  .portfolio-image{
      padding: 19px 0 10px 40px;      
  }

  .portfolio-item-links-big{
    margin-top: 110px;
  }

  .portfolio-item-description-big{
    margin-top: 80px;
  }


  .return-home{
    cursor: pointer;
  }

  .slick-slider img{
    cursor: pointer;
  }

  #portfolio-768{
    display: block;
  }

  #portfolio-min{
    display: none;
  }

}


@media (min-width: 1024px){   

  .header-outer{
    top: 30px;
  }

  .portfolio-image{
      padding: 19px 0 10px 60px;
  }  

  .nav{
      display: -ms-inline-flexbox;
      display: inline-flex;
      margin: 0;
  }

  .nav li{
      margin: 0 0 0 20px;
  }
  .intro{
    margin: 180px auto 80px;
    height: 270px;
  }

  .portfolio-image{
    float: right;
  }

  #portfolio-section{ 
    margin-top: 120px;
    z-index: 90;
    margin-bottom: 100px;
  }

  #portfolio{
      width: 90%;
      margin: 0 auto;
  }

  #skills{
      width: 90%;
  }

}


